.App {
  text-align: left;
  background-color: #282c34;
  color: white;
}

.Cart {
  position: sticky;
  top: 0;
  padding-left: 10px;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 18px;
}

.App-link {
  color: #61dafb;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  background-color: rgb(190, 194, 203);
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1;
}
